//
// Typography Placeholders
//

// These will throw a deprecation warning if used within a media query.
@mixin lead {
  font-size: $paragraph-font-size + rem-calc(3.5);
  line-height: 1.6;
}

@mixin subheader {
  line-height: $subheader-line-height;
  color: $subheader-font-color;
  font-weight: $subheader-font-weight;
  margin-top: $subheader-top-margin;
  margin-bottom: $subheader-bottom-margin;
}

@include exports("type") {
    /* Typography resets */
    div,
    dl,
    dt,
    dd,
    ul,
    ol,
    li,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    pre,
    form,
    p,
    blockquote,
    th,
    td {
      margin: 0;
      padding: 0;
    }
    *, html {
      @include ff-light;
    }
    /* Default Link Styles */
    a {
      color: $anchor-font-color;
      text-decoration: $anchor-text-decoration;
      line-height: inherit;
      &:hover,
      &:focus {
        color: $anchor-font-color-hover;
      }
      img {
        border: none;
      }
    }
    /* Default paragraph styles */
    p {
      font-family: $paragraph-font-family;
      font-size: $paragraph-font-size;
      line-height: $paragraph-line-height;
      margin-bottom: $paragraph-margin-bottom;
      text-rendering: $paragraph-text-rendering;
      &.lead {
        @include lead;
      }
      & aside {
        font-size: $paragraph-aside-font-size;
        line-height: $paragraph-aside-line-height;
        font-style: $paragraph-aside-font-style;
      }
    }
    /* Default header styles */
    h1, h2, h3, h4, h5, h6 {
      margin-top: $header-top-margin;
      margin-bottom: $header-bottom-margin;
      line-height: $header-line-height;
      small {
        font-size: $small-font-size;
        color: $small-font-color;
        line-height: 0;
      }
    }
    h1 {
      font-size: $h1-font-size - rem-calc(10);
    }
    h2 {
      font-size: $h2-font-size - rem-calc(10);
    }
    h3 {
      font-size: $h3-font-size - rem-calc(5);
    }
    h4 {
      font-size: $h4-font-size - rem-calc(5);
    }
    h5 {
      font-size: $h5-font-size;
    }
    h6 {
      font-size: $h6-font-size;
    }
    .subheader {
      @include subheader;
    }
    hr {
      border: $hr-border-style $hr-border-color;
      border-width: $hr-border-width 0 0;
      clear: both;
      margin: $hr-margin 0 $hr-margin - rem-calc(1);
      height: 0;
    }
    /* Helpful Typography Defaults */
    em,
    i {
      font-style: italic;
      line-height: inherit;
    }
    strong,
    b {
      font-weight: bold;
      line-height: inherit;
    }
    small {
      font-size: $small-font-size;
      line-height: inherit;
    }
    code {
      font-family: $code-font-family;
      font-weight: $code-font-weight;
      color: $code-color;
    }
    /* Lists */
    ul,
    ol,
    dl {
      font-size: $paragraph-font-size;
      line-height: $paragraph-line-height;
      margin-bottom: $paragraph-margin-bottom;
      list-style-position: $list-style-position;
      font-family: $paragraph-font-family;
    }
    ul {
      margin-#{$default-float}: $list-side-margin;
      &.no-bullet {
        margin-#{$default-float}: $list-side-margin-no-bullet;
        li {
          ul,
          ol {
            margin-#{$default-float}: $list-nested-margin;
            margin-bottom: 0;
            list-style: none;
          }
        }
      }
    }
    /* Unordered Lists */
    ul {
      li {
        margin-bottom:$list-item-margin-bottom;
        line-height:$list-item-line-height;
        ul,
        ol {
          margin-#{$default-float}: $list-nested-margin;
          margin-bottom: 0;
        }
      }
      &.square,
      &.circle,
      &.disc {
        li ul {
          list-style: inherit;
        }
      }
      &.square {
        list-style-type: square;
        margin-#{$default-float}: $list-side-margin;
      }
      &.circle {
        list-style-type: circle;
        margin-#{$default-float}: $list-side-margin;
      }
      &.disc {
        list-style-type: disc;
        margin-#{$default-float}: $list-side-margin;
      }
      &.no-bullet {
        list-style: none;
      }
    }
    /* Ordered Lists */
    ol {
      margin-#{$default-float}: $list-ordered-side-margin;
      li {
        margin-bottom:$list-item-margin-bottom;
        line-height:$list-item-line-height;

        ul,
        ol {
          margin-#{$default-float}: $list-nested-margin;
          margin-bottom: 0;
        }
      }
    }
    /* Definition Lists */
    dl {
      dt {
        margin-bottom: $definition-list-header-margin-bottom;
        font-weight: $definition-list-header-weight;
      }
      dd {
        margin-bottom: $definition-list-margin-bottom;
      }
    }
    /* Abbreviations */
    abbr,
    acronym {
      text-transform: uppercase;
      font-size: 90%;
      color: $body-font-color;
      border-bottom: $acronym-underline;
      cursor: $cursor-help-value;
    }
    abbr {
      text-transform: none;
    }
    /* Blockquotes */
    blockquote {
      margin: 0 0 $paragraph-margin-bottom;
      padding: $blockquote-padding;
      border-#{$default-float}: $blockquote-border;
      cite {
        display: block;
        font-size: $blockquote-cite-font-size;
        color: $blockquote-cite-font-color;
        &:before {
          content: "\2014 \0020";
        }
        a,
        a:visited {
          color: $blockquote-cite-link-color;
        }
      }
    }
    blockquote,
    blockquote p {
      line-height: $paragraph-line-height;
      color: $blockquote-font-color;
    }
    /* Microformats */
    .vcard {
      display: inline-block;
      margin: $microformat-margin;
      border: $microformat-border-width $microformat-border-style $microformat-border-color;
      padding: $microformat-padding;
      li {
        margin: 0;
        display: block;
      }
      .fn {
        font-weight: $microformat-fullname-font-weight;
        font-size: $microformat-fullname-font-size;
      }
    }
    .vevent {
      .summary {
        font-weight: $microformat-summary-font-weight;
      }
      abbr {
        cursor: $cursor-default-value;
        text-decoration: $microformat-abbr-font-decoration;
        font-weight: $microformat-abbr-font-weight;
        border: none;
        padding: $microformat-abbr-padding;
      }
    }
  
}